* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
}

.navbar-light a.navbar-brand,
.navbar-light a.navbar-brand:hover,
.primary-heading {
  font-weight: 900;
  font-size: 48px;
  line-height: 72px;
  letter-spacing: 1px;
  color: #121c33;
}

.nav-pills a.nav-link {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 8px 24px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  color: #121c33;
  opacity: 0.6;
  border-radius: 8px;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}

.nav-pills a.nav-link:focus,
.nav-pills a.nav-link:hover,
.nav-pills a.nav-link.active,
.nav-pills .show > a.nav-link {
  background: rgba(18, 28, 51, 0.05);
  color: #121c33;
  opacity: 1;
}

.tab-pane,
.tab-pane .row {
  position: relative;
}

div.card {
  background: #ffffff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  border: unset;
  padding: 16px;
  margin-top: 16px;
  margin-bottom: 16px;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

div.card.active {
  border: 2px solid #121c33;
}

div.card-char {
  padding: 0px;
  overflow: hidden;
}

div.card-header {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0px;
  border-radius: 0px;
  border: unset;
  background-color: transparent;
}

div.card-img-container {
  width: 120px;
  height: 120px;
  padding: 0px;
  margin-bottom: 16px;
}

div.card-img-container.char {
  width: 100%;
  height: 136px;
  margin-bottom: unset;
}

img.card-img {
  width: 100%;
  height: auto;
  background: radial-gradient(
    86.35% 66.25% at 76.67% 11.25%,
    #ffffff 0%,
    rgba(255, 255, 255, 0) 100%
  );
  background-blend-mode: soft-light;
  border-radius: 0px;
}

div.card-img-container.char img.card-img {
  height: 100%;
  /* width: 100%; */
}

div.card-body {
  display: flex;
  flex-direction: column;
  padding: 0px;
}

.card-char div.card-body {
  padding: 16px;
}

div.card-title {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #121c33;
}

div.card-description {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  color: #121c33;
  opacity: 0.6;
}

button.btn-add,
button.btn-add:focus,
button.btn-add:hover {
  position: absolute;
  bottom: 5%;
  right: 1%;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #121c33;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  border: unset;
}

button.btn-grey,
button.btn-grey:focus,
button.btn-grey:hover {
  background: rgba(18, 28, 51, 0.05);
  border-radius: 8px;
  padding: 12px 24px;
}

div.modal-content {
  padding: 48px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  border: unset;
  border-radius: 32px;
}

div.modal-header {
  border: unset;
}

div.modal-header button.btn-close {
  position: relative;
  width: 32px;
  height: 32px;
}

div.modal-header button.btn-close::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #121c33;
  opacity: 0.1;
  color: #121c33;
  border-radius: 8px;
}
